import mixins from "../../utils/mixins";
import LayoutTb from "../../components/Layout/LayoutTb";
import MyInputButton from "../../components/search/MyInputButton";
import MyPagination from "../../components/MyPagination";
import DialogNormal from "../../components/utils/dialog/DialogNormal";
import MyNormalDialog from "../../components/utils/dialog/MyNormalDialog";
import DrawerRight from "../../components/utils/dialog/DrawerRight";
import DrawerLayoutRight from "../../components/utils/dialog/DrawerLayoutRight";
import {common} from "../../utils/api/url";
import {MessageSuccess, MessageError, decodeUTF8} from "../../utils/utils";
import newsValidater from "../../utils/validater/newsValidater";

export default {
  mixins: [mixins, newsValidater],
  components: {LayoutTb,MyInputButton,MyPagination,DialogNormal,MyNormalDialog,DrawerRight,DrawerLayoutRight},
  data(){
    return {
      modalVisible: false,
      drawerVisible: false,
      visibleConfim: false,
      dialogLoading: false,
      loading: false,
      drawerLoading: false,
      drawerViewVisible: false,
      visibleReplyConfim: false,
      subTitle: '',
      noticeContentDetail: '',
      noticeContentDetailData: [],
      uploadFile: common.upload_file,
      uploadResult: {},
      uploadProcess: '',
      g_superId: '',
      tableData: [],
      replyId: '',
      replyIndex: '',
      tips: '',
      replyComment: '',
      msgRowData: {},
      addStatus: '',
      form: {
        id: '',
        title: '',
        titleDesc: '',
        content: '',
        type: 0,
        thumnbnail: ''
      },
    }
  },
  created() {
    this.initInfo();
  },
  methods: {
    async initInfo(){
      await this.getSessionInfo();
      this.init();
    },
    init(){
      let params = {
        page: this.page,
        num: this.num,
        type: 0,
        actionType: 2,
        draft: false
      };
      this.$axios.get(common.circular_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    initDetail(row){
      let params = {
        msgId: row.id
      };
      this.$axios.get(common.detail_circle_search, {params: params}).then(res => {
        if (res.data.data){
          this.noticeContentDetail = res.data.data;
        }
      });
    },
    addInfo(){
      if (this.addStatus == 'edit'){
        this.clearDrawerDialog();
      }
      this.drawerVisible = true;
    },
    editInfo(row){
      this.addStatus = 'edit';
      this.form = {
        id: row.id,
        title: row.title,
        titleDesc: row.title_desc,
        content: row.content,
        type: 0,
        thumnbnail: row.title_img
      };
      this.drawerVisible = true;
    },
    deleteInfo(row){
      this.form.id = row.id;
      this.subTitle = row.title;
      this.visibleConfim = true;
    },
    cancelDialog(){
      this.modalVisible = false;
    },
    cancelDrawDialog(){
      this.clearDrawerDialog();
      this.drawerVisible = false;
    },
    closeDialog(event){
      this.form = {
        id: '',
        title: '',
        titleDesc: '',
        content: '',
        type: 0,
        thumnbnail: ''
      };
      this.dialogLoading = false;
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    clearDrawerDialog(){
      this.form = {
        id: '',
        title: '',
        titleDesc: '',
        content: '',
        type: 0,
        thumnbnail: ''
      };
      this.replyComment = '';
      this.msgRowData = {};
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
      this.errorTips = "";
      this.addStatus = '';
    },
    closeDrawerDialog(event){
      this.drawerVisible = event;
      this.drawerViewVisible = event;
    },
    handleCloseDrawer(){
      this.form = {
        id: '',
        title: '',
        titleDesc: '',
        content: '',
        type: 0,
        thumnbnail: ''
      };
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    closeDrawDialog(event){
      this.drawerVisible = false;
    },
    okDrawDialog(event){
      let url = "";
      this.$refs['form'].validate((valid) => {
        if (valid) {
          let params = {
            title: this.form.title,
            titleDesc: this.form.titleDesc,
            content: this.form.content,
            actionType: 2,
            titleImg: this.form.thumnbnail,
          };

          if (this.form.id != ""){
            params['id'] = this.form.id;
          }

          url = common.circular_add;
          //params = this.$qs.stringify(params);
          this.drawerLoading = true;
          this.$axios.post(url, params, {dataType: 'stringfy'}).then(res => {
            if (res.data.code == 200){
              this.drawerVisible = false;
              this.init();
              this.clearDrawerDialog();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.drawerLoading = false;
          });
        }
      });
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let params = {
        msgId: this.form.id
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.circular_delete, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
      this.visibleReplyConfim = false;
    },
    search(event){

    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    quillChange(data){
      this.form.content = data.html;
    },
    uploadFileSuccess(res, file){
      if (res.code == 200){
        this.form.thumnbnail = res.data.url;
      }else {

      }
    },
    closeImg(){
      this.form.thumnbnail = "";
    },
    decodeUTF8Info(val){
      return decodeUTF8(val);
    },
    detailMsg(row){
      let url = common.detail_circle_search_reply;
      let params = {
        page: 1,
        num: 999,
        msgId: row.id,
        userId: this.loginUserId
      };

      this.noticeContentDetailData = [];
      this.$axios.get(url, {params: params}).then(res => {
        if (res.data.data){
          for (let i = 0; i < res.data.data.list.length; i++){
            res.data.data.list[i]['replyModal'] = false;
            res.data.data.list[i]['replyOtherModal'] = false;
          }
          this.noticeContentDetailData = res.data.data.list;
        }
      });
      this.msgRowData = row;
      this.initDetail(row);
      this.drawerViewVisible = true;
    },
    deleteReply(data,index){
      this.tips = "确认需要删除该回复吗？";
      this.replyId = data.id;
      this.replyIndex= index;
      this.visibleReplyConfim = true;
    },
    handleOkReplyChange(data) {
      this.dialogLoading = true;
      let url = common.reply_delete;
      let params = {
        commentId: this.replyId
      };
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          //this.noticeContentDetailData.splice(this.replyIndex,1);
          this.detailMsg(this.msgRowData);
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleReplyConfim = false;
        this.dialogLoading = false;
      });
    },
    replyContent(data, index){
      this.dialogLoading = true;
      let url = common.reply_add;
      let params = {
        comment: this.replyComment,
        msgId: data.msg_id,
        type: 2,
        commentId: data.id,
      };
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.detailMsg(this.msgRowData);
          this.init();
          data.replyModal = false;
          data.replyOtherModal = false;
          this.$set(data, 'replyModal', false);
          this.replyComment = "";
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.dialogLoading = false;
      });
    },
    hideReplyModal(){
      this.replyComment = "";
    },
    replyOhterModalOpr(item, index){
      this.$set(this.noticeContentDetailData[index], 'replyModal', true);
      this.$set(item, 'replyModal', true);
    },
    replyModalOpr(item, index){
      this.$set(this.noticeContentDetailData[index], 'replyModal', true);
      this.$set(item, 'replyModal', true);
    }
  }
}
