import mixins from "../../../utils/mixins";
import {oneOf} from "../../../utils/utils";
import {common} from "../../../utils/api/url";
export default {
  mixins: [mixins],
  props: {
    title: {
      default: '信息',
      type: String
    },
    visible: {
      default: false,
      type: Boolean
    },
    direction: {
      default: 'rtl',
      type: String
    },
    hideFooter: {
      default: false,
      type: Boolean
    },
    loading: {
      default: false,
      type: Boolean
    },
    size: {
      defalult: '30%',
      type: String
    },
    action: {
      default: '',
      type: String
    },
    downloadFile: {
      default: '',
      type: String
    },
    accept: {
      default: '',
      type: String
    },
    data: {
      default: function () {
        return {};
      },
      type: Object
    },
    process: {
      default: '',
      type: String
    },
    result: {
      default: function () {
        return {} || [];
      },
      type: [Object, Array]
    },
    setBefore: {
      default: false,
      type: Boolean
    }
  },
  computed: {
    drawer_:{
      get(){
        return this.visible
      },
      set(v){
        this.$emit("changeDrawer",v)
      }
    }
  },
  data(){
    return {
      drawer: false,
      dialogNormalVisible: false,
      footer: false,
      header: false,
      drawerLoading: false
    }
  },
  mounted() {
    this.dialogNormalVisible = this.visible;
    this.footer = this.$slots.footer !== undefined;
    this.header = this.$slots.title !== undefined;
  },
  created() {

  },
  methods: {
    handleClose(data){
      if (!this.footer){
        this.$emit("right-close", data);
      }else {
        this.handleCancel();
      }
    },
    handleOk(data){
      this.$emit("ok", data);
    },
    handleCancel(data){
      this.$emit("cancel", data);
    },
    download(event){
      window.open(this.downloadFile, '_self');
    },
    handleAvatarSuccess(res, file){
      this.$emit('success', res, file);
    },
    handleAvatarError(res, file){
      this.$emit('error', res, file);
    },
    handleAvatarBefore(file){
      if (this.setBefore){
        this.data['fileName'] = file.name;
      }
    }
  }
}
